<template>
    <div>
        <p class="pad-20">该文件介绍http.js的使用方法,提供了常用的异步请求方法；</p>
        <el-tabs :tab-position="'left'">
            <el-tab-pane label="get请求">
                <pre class="pre-detail">
            let data = {params1:'xxx',params2:''};
            this.$get('请求url', data).then((res) => {
            //请求结果处理
            })
                </pre>
            </el-tab-pane>
            <el-tab-pane label="post请求">
                <pre class="pre-detail">
            let data = {params1:'xxx',params2:''};
            1、常规data数据请求
            this.$post('请求url', data).then((res) => {
                //请求结果处理
            })
            2、data为json是的请求
            this.$post('请求url', data, {
                headers: { 'Content-Type': 'application/json;', 'Accept': '*/*' }
            }).then((res) => {
            //请求结果处理
            })
            3、data为formData
                需要这样处理：
                let data = new FormData();
                data.append('params',paramsValue);  
                </pre>
            </el-tab-pane>
            <el-tab-pane label="axios请求">
                <pre class="pre-detail">
            当遇到特殊情况的时候，可以使用this.$axios();这种形式，参数形式参考axios官方文档
                </pre>
            </el-tab-pane>
            <el-tab-pane label="其他">
                <pre class="pre-detail">
            1、默认的baseURL为'/devServer',可在单个请求时配置独立的baseURL,
            如：
            this.$post('请求url', data, {
                baseURL: '/defaultBaseURL'
            }).then((res) => {
            //请求结果处理
            })
            2、配置请求的其他参数，也可采用这种形式，配置在请求的第三个参数中
